* {
  margin: 0;
  padding: 2px;
  font-family: Helvetica, Arial, sans-serif;
  background-color: #2c3e50;
  color: #ecf0f1;
}

h1, p, div {
  text-align: center;
  margin-top: 2vh;
}

.topText {
  font-family: Helvetica, Arial, sans-serif;
}

.pageText {
  font-size: 15px;
  padding-bottom: 5px;
}

.icon-btn {
  padding: 0 10px;
  font-size: 4em;
}

.fire {
  color: #e74c3c;
}

.wind {
  color: #9b59b6;
}

.earth {
  color: #d35400;
}

.water {
  color: #3498db;
}

.ncpButtons {
  margin-left: -10px;
}

.action-btn {
  border: none;
  outline: none;
  padding: 10px 50px;
  margin-top: 10px;
  border-radius: 100px;
  font-size: 1.11em;
}

.fire-btn {
  background-color: #e74c3c;
}

.wind-btn {
  background-color: #9b59b6;
}

.earth-btn {
  background-color: #d35400;
}

.water-btn {
  background-color: #3498db;
}

.rollResults {
  margin-top: 15px;
}